  .banner{
    height: 25vh;
  }
  .header-icons-wrap .cart-amount{
    width: 30px;
  }
  .header-link span,
  .nav-toggle span{
    font-size: .6rem;
  } 
}
@media (max-width: 767px){
  *{
    font-size: .9rem;
  }
  .nav-toggle span{
    display: none;
  }
  .banner{
    height: 20vh;
  }
  .banner-title{
    font-size: 1.7rem;
  }
  .box-hover{
    left: 5%;
  }
  .banner-inner-wrap .banner-btn{
    width: 40%;
    font-size: .8rem;
  }
  .btn-custom{
    width: 100%;
  }
}
@media (max-width: 575px){
  *{
    font-size: .8rem;
  }
  #nav{
    width: 200px;
    min-width: 200px;
  }
  .nav-toggle span{
    display: none;
  } 
  [id='nav-toggle']:checked ~ .nav-toggle {
    left: 150px;
  }
  .header-icons-wrap .cart-amount{
    width: 25px;
  }
  .banner{
    height: 15vh;
  }
  .banner-inner-wrap{
    width: auto;
  }
  .banner-title{
    font-size: 1.5rem;
  }
  .banner-inner-wrap .banner-btn{
    width: 40%;
    font-size: .8rem;
    border-width: 1px;
  }
  .card-img-link-wrap{
    height: auto;
  }
  .card-title>a{
    font-size: 1.3rem;
  }
  .card .meta.author{
    font-size: 1rem;
  }
  .card .meta.price{
    font-size: 1.3rem;
  }
  .table .btn-custom{
    font-size: .5rem;
    margin-top: unset;
  }
  .quantity-btn{
    font-size: 1.5rem;
  }
  .quantity-input{
    width: 30px;
    text-align: center;
    font-weight: bold;
  }
  .meta.author {
    font-size: .6rem;
  }
}
